import {mapState} from 'vuex';
export default {
	data() {
		return {
			userInfo:""
		};
	},
	mounted(){
		let userInfo = uni.getStorageSync("userInfo");
		// console.log(userInfo)
		this.userInfo=userInfo;
	},
	computed:{
		...mapState({
			cartList:state=>state.cart.cartList
		})
	},
	methods:{
		toLogin(){
			uni.navigateTo({
				url:"/pages/login/login"
			})
		}
	}
}
